<template>
    <el-container style="height: 100%; width: 100%">
        <Aside />
        <el-container>
            <el-header style="height: 42px;">
                <tabs />
            </el-header>
            <el-main>
                <keep-alive>
                    <router-view exact></router-view>
                </keep-alive>
            </el-main>
        </el-container>
    </el-container>
</template>
<script>
import PageBase from '@/mixins/pageBase'
import Aside from './components/aside'
import Tabs from './components/tabs'
import { mapState } from 'vuex'
export default {
    mixins: [PageBase],
    components: {
        Aside,
        Tabs
    },
    data() {
        return {
            isUnregisterStore: false
        }
    },
    created() {
        this.$store.dispatch('app/getMenus')
        this.defaultRoute()
    },
    computed: {
        ...mapState('app', ['tabs'])
    },
    methods: {
        closeTab(code) {
            this.$store.commit('app/closeTab', { code })
        },
        activeTab(code) {
            this.$store.commit('app/activeTab', { code })
        },
        defaultRoute() {
            const route = this.getRouteByPath(this.$route.path)
            if (route) {
                this.activeTab(route.meta.code)
            }
        },
    }
}
</script>
<style lang="scss" scoped>
/deep/ .el-tag {
    border-radius: 0%;
    height: 100%;
    color: #f00;
    border-color: #f00
}
</style>
