import axios from 'axios'
import qs from 'qs'
import mockJs from 'mockjs'
const isAllowMock = process.env.VUE_APP_API_ISALLOWMOCK

mockJs.setup({
    timeout: '200-400'
})

axios.defaults.baseURL = process.env.VUE_APP_API_PREFIX

// 请求拦截
axios.interceptors.request.use(function(config) {
    // do something for data
    return config
}, function(error) {
    return Promise.reject(error)
})

// 响应拦截
axios.interceptors.response.use(function(response) {
    // do something for data
    return response
}, function(error) {
    if (error.response.status === 401) {
        location.href = error.response.data + '?returnUrl=' + encodeURIComponent(location.href)
    }
    return Promise.reject(error)
})

const CONTENT_TYPES = {
    0: 'application/json',
    1: 'multipart/form-data',
    2: 'application/x-www-form-urlencoded'
}

const PENDINGS = []
export function ajax({ method = 'post', url, data = {}, tc = 0, t = false, other = {} }) {
    const pending = `${method}${url}${JSON.stringify(data)}`
    const index = PENDINGS.findIndex(item => item === pending)
    if (index !== -1) {
        PENDINGS.splice(index, 1)
        return Promise.reject({
            msg: '亲，您的操作太频繁了呦，给服务器一点喘气的机会吧',
            code: -1
        })
    }
    PENDINGS.push(pending)
    const opt = {
        method,
        url,
        headers: { 'Content-Type': CONTENT_TYPES[tc] },
        ...other
    }
    if (method === 'get') {
        const timestamp = new Date().getTime()
        data.t = t ? timestamp : undefined
        opt['params'] = data
    } else if (method === 'post') {
        if (tc === 2) {
            opt['data'] = qs.stringify(data)
        } else {
            opt['data'] = data
        }
    }
    return axios(opt).then(({status, data}) => data).finally(function() {
        let timerId = setTimeout(function() {
            PENDINGS.splice(index, 1)
            clearTimeout(timerId)
        }, 1)
    })
}

function getApiUrl(prefix, item) {
     return (
         process.env.NODE_ENV === 'development' && item.devMock
             ? process.env.VUE_APP_MOCK_PREFIX + (item.prefix || prefix || '') + item.url
             : (item.prefix || prefix || '') + item.url
     )
}

export function generator({ prefix, items = [] }) {
    const map = {}
    for (let i = 0, len = items.length; i < len; i++) {
        const item = items[i]
        map[item['key']] = function(data) {
            const fields = ['key', 'method', 'url', 'prefix']
            const other = JSON.parse(JSON.stringify(item))
            fields.forEach(item => { delete other[item] })
            return ajax({
                method: item['method'] || 'post',
                url: (item.prefix || prefix || '') + item.url,
                data,
                other
            })
        }
    }
    return map
}
