<template>
    <el-tabs type="card" closable @tab-remove="removeTab" @tab-click="activeTab" :value="currentCode">
        <el-tab-pane v-for="item of tabs" :key="item.code" :label="item.name" :name="item.code">
            <span slot="label"><i class="el-icon-date"></i>{{item.name}}</span>
        </el-tab-pane>
    </el-tabs>
</template>
<script>
import { mapState } from 'vuex'
export default {
    computed: {
        ...mapState('app', ['tabs', 'currentCode'])
    },
    methods: {
        removeTab(code) {
            this.$store.commit('app/closeTab', { code })
        },
        activeTab({ name: code }) {
            this.$store.commit('app/activeTab', { code })
        }
    }
}
</script>