/**
 * 局部store的注册卸载
 */
export default {
    data() {
        return {
            isUnregisterStore: true
        }
    },
    beforeDistory() {
        this.unregisterStore()
    },
    methods: {
        dispatch(type, payload) {
            const partialStore = this.storeKey ? (this.storeKey + '/' + type) : type
            this.$store.dispatch(partialStore, payload)
        },
        commit(type, payload) {
            const partialStore = this.storeKey ? (this.storeKey + '/' + type) : type
            this.$store.commit(partialStore, payload)
        },
        // 注册私有store
        registerStore(store) {
            if (!store) return
            if (store && !this.storeKey) {
                throw new Errow({message: 'set storeKey in data of current module, pleace'})
            }
            this.$store.registerModule(this.storeKey, store)
        },
        // 卸载私有store
        unregisterStore() {
            if (this.isUnregisterStore && this.storeKey) {
                this.$store.unregisterModule(this.storeKey, store)
            }
        }
    }
}