<template>
    <div class="aside">
        <el-menu
            :collapse="collapse"
            :default-openeds="defaultOpeneds"
            :default-active="currentCode"
            @select="activeMenu" >
                <el-submenu v-for="item in menus" :key="item.id" :index="item.code">
                    <template slot="title">
                        <i :class="item.icon || 'el-icon-menu'"></i>
                        <span slot="title">{{item.name}}</span>
                    </template>
                    <el-menu-item-group v-if="item.children && item.children.length !== 0">
                        <el-menu-item v-for="subItem in item.children" :key="subItem.id" :index="subItem.code">{{subItem.name}}</el-menu-item>
                    </el-menu-item-group>
                </el-submenu>
        </el-menu>
        <div class="ft">
            <el-button @click="handleLogout">退出</el-button>
        </div>
        <span :class="{'menu-middle': true, 'el-icon-caret-left': collapse, 'el-icon-caret-right': !collapse }" @click="visibleMenu"></span>
    </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
    data() {
        return {
            collapse: false
        }
    },
    computed: {
        ...mapState('app', [ 'defaultOpeneds', 'menus', 'currentCode'])
    },
    methods: {
        activeMenu(code) {
            this.$store.commit('app/activeTab', { code })
        },
        // 退出登录
        handleLogout() {
            
        },
        visibleMenu() {
            this.collapse = !this.collapse
        }
    }
}
</script>
<style lang="scss" scoped>
// 最好可以自己布局，不使用el-aside, 坑比较多
    $aside-theme: rgba($color: #83b0f3, $alpha: 1.0);
    $menu-hover-bg: rgb(119, 157, 228);
    .aside {
        background-color: $aside-theme;
        position: relative;
        .menu-middle {
            position: absolute;
            top: 50%;
            left: 100%;
        }
    }
    /deep/ .el-menu {
        background-color: $aside-theme;
        .el-submenu__title:hover, .el-menu-item:hover {
            background-color: $menu-hover-bg;
        }
        .el-submenu__title:focus, .el-menu-item:focus {
            background-color: $menu-hover-bg;
        }
        /deep/ .is-active {
           color: #000;
        }
    }
    .ft {
        position: absolute;
        bottom: 0;
        width: 100%;
    }
    .ft  /deep/ .el-button {
        width: 100%;
    }
    
</style>