import StoreBase from './storeBase'
export default {
    mixins: [StoreBase],
    computed: {
        app() {
            return this.$store.state.app
        }
    },
    methods: {
        getRouteByPath(path) {
            for (let i = 0, routeArr = this.app.routeArr, len = routeArr.length; i < len; i++) {
                if (routeArr[i].path === path) {
                    return routeArr[i]
                }
            }
        }
    }
}
