/**
 * store中通用的异步方法
 */
export function generatorActions() {
    const actions = {}
    // merge some function async to store
    return actions
}

/**
 * store中通用的同步方法
 */
export function generatorMutations() {
    const mutations = {}
    mutations.update = function (state, payload) {
        for (let prop in payload) {
            state[prop] = payload[prop]
        }
    }
    return mutations
}