class Utils {
    constructor() {
        if (new.target === U) {
            throw new Error('这是一个工具类,不建议进行实例化')
        }
    }
    // static init() {
    //     this.dataTypes = {
    //         obj: '[object Object]',
    //         arr: '[object Array]',
    //         str: '[object String]',
    //         num: '[object Number]',
    //         bool: '[object Boolean]',
    //         un: '[object Undefined]',
    //         sym: '[object Symbol]',
    //         null: '[object Null]',
    //         fn: '[object Function]',
    //         reg: '[object RegExp]'
    //     }
    //     return this
    // }
    static getDataType(data) {
        return Object.prototype.toString.apply(data)
    }
    static isArray(data) {
        return this.getDataType(data) === this.dataTypes.arr
    }
    static isObject(data) {
        return this.getDataType(data) === this.dataTypes.obj
    }
    static isString(data) {
        return this.getDataType(data) === this.dataTypes.str
    }
    static isNumber(data) {
        return this.getDataType(data) === this.dataTypes.num
    }
    static isBool(data) {
        return this.getDataType(data) === this.dataTypes.bool
    }
    static isSymbol(data) {
        return this.getDataType(data) === this.dataTypes.sym
    }
    static isUndefined(data) {
        return this.getDataType(data) === this.dataTypes.un
    }
    static isNull(data) {
        return this.getDataType(data) === this.dataTypes.null
    }
    static isFunction(data) {
        return this.getDataType(data) === this.dataTypes.fn
    }
    static isRegExp(data) {
        return this.getDataType(data) === this.dataTypes.reg
    }
    static dom(selector) {
        if (!selector) {
            return
        }
        let mark = selector.slice(0, 1)
        let elNode
        switch (mark) {
            case '#':
                elNode = document.getElementById(selector)
            break
            case '.':
                elNode = document.getElementsByClassName(selector)
            break
            default:
                elNode = document.querySelectorAll(selector)
            break
        }
        return elNode
    }
    // 判断变量是否为空
    static isEmpty(data) {
        if (this.isArray(data)) {
            return data.length === 0 ? true : false
        }
        if (this.isObject(data)) {
            let keys = Object.keys(data)
            return keys.length === 0 ? true : false
        }
        if (this.isString(data)) {
            data = data.trim()
            return data.length === 0 ? true : false
        }
        if (this.isNumber(data)) {
            return false
        }
        if (this.isNull(data) || this.isUndefined(data)) {
            return true
        }
    }
    // 移除对象下的空属性
    static removeEmptyProp(data) {
        if (!this.isObject(data)) {
            throw new TypeError('not is object')
        }
        for (let prop in data) {
            let value = data[prop]
            if (this.isNull(value) || this.isUndefined(value)) {
                delete data[prop]
            } else if (this.isObject(data)) {
                this.removeEmptyProp(data[prop])
            }
        }
    }
    // 下载
    static download(url, params) {
        if (!url) {
            throw new Error('not a url')
        }
        if (this.isObject(params)) {
            this.removeEmptyProp(params)
            if (!this.isEmpty(params)) {
                let str = ''
                for (let prop in params) {
                    str = prop + '=' + params[prop] + '&'
                }
                str = str.slice(0, -1)
                url = url + '?' + str
            }
        }
        open(url)
    }
    // 深复制
    static cloneDeep(sourceObj) {
        let targetObj = new sourceObj.constructor()
        let keys = Object.getOwnPropertyNames(sourceObj)
        for (let i = 0, len = keys.length; i < len; i++) {
            let desc = Object.getOwnPropertyDescriptor(sourceObj, keys[i])
            if (this.isRegExp(desc.value)) {
                console.log(1)
                let targetRegExp = new desc.value.constructor(desc.value.source, desc.value.flags)
                Object.defineProperty(targetObj, keys[i], {
                    configurable: desc.configurable,
                    writable: desc.writable,
                    enumerable: desc.enumerable,
                    value: targetRegExp
                })
            } else if (this.isObject(desc.value) && !this.isNull(desc.value)) {
                console.log(desc, typeof desc.value)
                Object.defineProperty(targetObj, keys[i], {
                    configurable: desc.configurable,
                    enumerable: desc.enumerable,
                    writable: desc.writable,
                    value: this.cloneDeep(desc.value)
                })
            } else {
                console.log(3)
                Object.defineProperty(targetObj, keys[i], desc)
            }
        }
        return targetObj
    }
    // 随机颜色
    static randomColor() {
        let color = '#'
        for (let i = 0; i < 6; i++) {
            color += Math.floor(Math.random()*16).toString(16)
        }
        return color
    }
    // 删除对象下的某些属性
    static deleteProps(obj, props) {
        props.forEach(prop => { delete obj[prop] })
    }
    // 防抖
    static debounce(_this, fn, time) {
        let timer = null
        return function() {
            if (timer) {
                clearTimeout(timer)
            }
            timer = setTimeout(fn.bind(_this), time)
        }
    }
    // 节流
    static throttle(_this, fn, time) {
        let timer = null
        return function() {
            if (timer) return
            timer = setTimeout(function() {
                fn.call(_this)
                clearTimeout(timer)
                timer = null
            }, time)
        }
    }
    // 数组乱序
    static disorder(arr) {
        return arr.sort(() => Math.random() - 0.5)
    }
    /**
     * 获取当前月天数或最后一天
     * @param {number|string} y -当前年,eg:2020
     * @param {number} m -当前月,eg:1
     * @return {number} -当前月天数
     * */
    static getCurMonDay(y, m) {
        if (!y || !m) {
            throw Error('params: year and month be must!')
        }
        typeof y === 'string' && (y = Number(y))
        if (m === 12) {
            m = 1
            y = y + 1
        }
        const dayTime = 1000 * 60 * 60 * 24
        const nextDay = `${y}-${m < 10 ? '0' + m : m}-01`
        const lastDay = new Date(new Date(nextDay).getTime() - dayTime).getDate()
        return lastDay
    }

}

Utils.dataTypes = {
    obj: '[object Object]',
    arr: '[object Array]',
    str: '[object String]',
    num: '[object Number]',
    bool: '[object Boolean]',
    un: '[object Undefined]',
    sym: '[object Symbol]',
    null: '[object Null]',
    fn: '[object Function]',
    reg: '[object RegExp]'
}

export default Utils
