import U from '@/utils/utils'
import router from '@/routes'
import { generatorMutations, generatorActions } from '@/store/helper'

/**
 * 根据菜单获取路由
 * @param {Array} menus
 */
function getRoutes(menus) {
    const routeArr = []
    const routeMap = {}
    function cover(menu) {
        menu.forEach(cmenu => {
            const route = {
                name: cmenu.name,
                path: cmenu.url,
                meta: { id: cmenu.id, code: cmenu.code, moduleName: cmenu.name, path: cmenu.url, isGlobal: cmenu.isGlobal || false },
                component: () => import ('@/modules' + cmenu.url + '/index.vue')
            }
            routeArr.push(route)
            routeMap[cmenu.code] = route
            cmenu.children = cmenu.children || []
            cover(cmenu.children)
        })
    }
    menus.forEach(item => {
        if (item.children && item.children.length) {
            cover(item.children)
        }
    })
    router.addRoutes(routeArr)
    return { routeArr, routeMap }
}

const app = {
    // 空间命名，找到相应模块
    namespaced: true,
    state: {
        name: 'app',
        menus: [],
        defaultOpeneds: [],
        routeArr: [],
        routeMap: {},
        currentCode: null,
        tabs: [],
        isPrint: false
    },
    actions: {
        ...generatorActions(),
        async getMenus({ state, commit }) {
            const {code, data, msg} = await api.menu.getMenus()
            if (code === 200) {
                const { routeArr, routeMap } = getRoutes(data)
                commit('update', { routeArr, routeMap, menus: data })
            } else {
                this._vm.$message.error(msg)
            }
        }
    },
    mutations: {
        ...generatorMutations(),
        activeTab(state, { code, query }) {
            if (!code) return
            if (state.currentCode === code) return
            const { moduleName, path } = state.routeMap[code].meta
            if (!state.currentCode) {
                state.currentCode = code
                state.tabs.push({ code, name: moduleName })
                router.push({ path, query })
                return
            }
            const route = state.tabs.find(item => item.code === code)
            if (!route) {
                state.currentCode = code
                state.tabs.push({ code, name: moduleName })
                router.push({ path, query })
            } else {
                state.currentCode = code
                router.push({
                    path: state.routeMap[code].path,
                    query
                })
            }
        },
        closeTab(state, { code }) {
            const index = state.tabs.findIndex(item => item.code === code)
            state.tabs.splice(index, 1)
            if (state.tabs.length === 0) {
                state.currentCode = null
                router.push('/')
            } else {
                const lastTab = state.tabs[state.tabs.length-1]
                state.currentCode = lastTab.code
                router.push(state.routeMap[lastTab.code].path)
            }

        },
        updatePrint(state, isPrint) {
            state.isPrint = isPrint || false
        }
    }
}
export default app
